body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.modal-content {
  background-color: black;
  border-radius: 0px;
  border-color: white;
}

.modal-header .modal-title {
  color: white;
}

.modal-body .form-group .form-control {
  background-color: black;
  color: white;
  border-radius: 0px;
  border-color: white;
}

.modal-footer .mdl-btn {
  color: white;
  background-color: black;
  font-weight: bold;
}

.modal-footer .mdl-btn-close:active {
  background-color: gray;
}

.modal-footer .mdl-btn-primary {
  background-color: blue;
}

.modal-footer .mdl-btn-primary:active {
  background: steelblue;
}
.btn, .dropdown-menu {
    border-radius: 0px;
}
.header-banner-container {
    width: 100%;
    display: flex;
}

.header-content-alignment {
    display: flex;
    flex: 1 1;
    height: 100px;
    align-items: center;
}

.header-logo {
    color: white;
    flex: 33% 1;
    text-align: left;
    margin-left: 25px;
}

.header-action-items {
    flex: 20% 1;
    text-align: center;
}

.header-navbar {
    flex: 0% 1;
    list-style: none;
}


.carousel-item-inner-container {
    background-color: white;
}
.navbar-container .caret {
    display: none;
}

.navbar-container .dropdown-toggle {
    text-decoration: none;
    color: white;
    font-size: 34px;
}

.navbar-container .dropdown-menu {
    border-color: gray;
    background-color: black;
}

.navbar-container .dropdown-menu a {
    color: white;
}
.content-text {
    margin: 0% 25% 0% 25%;
}
.footer-main {
    width: 100%;
    height: 250px;
    background-color: black;
}

.footer-logo > img {
    width: 100px;
    height: 100px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    margin-left: 25px;
    margin-right: 20px;
}

.footer-logo > span {
    font-size: x-large;
    font-weight: bold;
    color: white;
}

.home-container {
    display: flex;
    flex-direction: column;
}

.home-header-container {
    flex: 1 1;
    position: fixed;
    width: 100%;
    z-index: 999;
    background-color: black;
}

.home-header-container-color-top {
    opacity: 1;
}

.home-header-container-color-scroll {
    opacity: 0.85;
}

.home-carousel-container {
    flex: 1 1;
    margin-top: 100px;
}

.home-content {
    flex: 1 1;
    text-align: center;
    padding-bottom: 8%;
    background-color: white;
}

.home-content > div {
    margin-top: 8%;
}
